<template>
  <div class="publish">
    <div class="article">
      <div class="title">发布标注任务</div>
      <el-form v-model="task" label-width="110px" label-position="left" size="medium" ref="taskForm">
        <el-form-item label="任务名称" prop="title">
          <el-input v-model="task.title" placeholder="任务名称"></el-input>
        </el-form-item>
        <el-form-item label="任务描述">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2}"
            placeholder="任务描述"
            v-model="task.description">
          </el-input>
        </el-form-item>
        <el-form-item label="任务类型" prop="taskType">
          <el-radio-group v-model="task.taskType">
            <el-radio :label="0">分类标注</el-radio>
            <el-radio :label="1">标框标注</el-radio>
            <el-radio :label="2">区域标注</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="分类标注标签" v-show="task.taskType === 0" prop="labels">
          <el-form size="small" class="label-form" v-model="task.labels">
            <el-form-item :label="'标签'+(index+1)" v-for="(label,index) in task.labels" :key="index">
              <el-input v-model="task.labels[index]"></el-input>
              <el-button type="danger" class="delete-btn" @click="deleteLabel(index)">删除</el-button>
            </el-form-item>
            <el-button plain @click="addLabel">添加标签</el-button>
          </el-form>
        </el-form-item>
        <el-form-item label="上传图片" prop="images">
          <el-upload
            drag
            action="/upload"
            :data="uploadData"
            multiple
            :on-success="addImage"
            :file-list="filelist">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div class="btn-group">
        <el-button type="primary" class="publish-btn" @click="publish">发布</el-button>
        <el-button type="plain" class="cancel-btn" @click="cancel">取消</el-button>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'publish',
    data () {
      return {
        uploadData: {
          id: 0
        },
        filelist: [],
        task: {
          title: '',
          description: '',
          taskType: 0,
          labels: [],
          images: []
        }
      }
    },
    created () {
      this.$ajax.get('/tasks/new').then((res) => {
        res = res.data
        console.log(res)
        if (res.code === 0) {
          this.uploadData.id = res.data
        }
      })
    },
    methods: {
      addLabel: function () {
        this.task.labels.push('')
      },
      deleteLabel: function (index) {
        this.task.labels.splice(index, 1)
      },
      addImage: function (response, file, fileList) {
        this.task.images.push({
          filename: file.name
        })
        this.filelist.push({
          name: file.name
        })
      },
      publish: function () {
        this.$ajax.post('/tasks', this.task).then((res) => {
          if (res.data.code === 0) {
            this.$message.success('发布成功')
          } else {
            this.$message.error('发布失败')
          }
        }).catch(() => {
          this.$message.error('发布失败')
        })
      },
      cancel: function () {
        this.$confirm('确定放弃本次发布任务吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push('/home')
        }).catch(() => {

        })
      }
    }
  }
</script>

<style lang="stylus">
  .publish
    padding 50px 0 100px 0
    .article
      position: relative;
      margin 0 auto
      width 800px
      padding 50px 100px 100px 100px
      background: #fff
      .el-input
        width 300px
      textarea
        width 600px
      .title
        margin-bottom 50px
        padding-left 15px
        border-left 3px solid #409eff
        color: #000
        font-size 20px
      .label-form
        .el-form-item__label
          line-height 30px
        .delete-btn
          margin-left 10px
          height 30px
      .btn-group
        position: absolute
        bottom 50px
        left 200px
        .publish-btn
          margin-right 20px
</style>



