<template>
  <div class="login">
    <div class="form">
      <div class="tab-head" slot="head">
        <router-link to="/login">登录</router-link>
        <router-link to="/sign">注册</router-link>
      </div>
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'login'
  }
</script>

<style lang="stylus">
  .login
    width: 100%
    height 100vh
    display flex
    justify-content center
    align-items center
    background-image url("login-bg.jpg")
    background-repeat no-repeat
    background-size 100% 100%
    .form
      width 100%
      .tab-head
        width 25%
        padding 2% 5% 0 5%
        margin auto
        text-align center
        background #fff
        a
          margin-right: 10px
          font-size 20px
          &.active
            color #409eff

</style>



