<template>
  <div class='tasks'>
    <div class="task-wrapper">
      <h1 class="title">任务列表</h1>
      <div class="tasks">
        <div class="task-card" v-for="(taskInfo,index) in taskInfos" :key="index" @click="enterTask(index)">
          <el-card :body-style="{ padding: '0px' }">
            <img class="image" :src="'/taskdata/'+ taskInfo.id + '/' + taskInfo.picName">
            <div class="content">
              <div class="task-title">{{ taskInfo.title }}</div>
              <div class="info-blocks">
                <div class="block">
                  <div class="label">总题数</div>
                  <div class="num"><strong>{{ taskInfo.picNum }}</strong><span class="unit">题</span></div>
                </div>
                <div class="block">
                  <div class="label">类型</div>
                  <div class="num"><strong>{{ taskType(taskInfo.taskType) }}</strong></div>
                </div>
              </div>

            </div>
          </el-card>
        </div>
      </div>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  export default {
    name: 'tasks',
    data () {
      return {
        taskInfos: []
      }
    },
    created () {
      this.$ajax.get('/tasks/list').then((response) => {
        response = response.data
        this.taskInfos = response.data
      })
    },
    methods: {
      enterTask: function (index) {
        let taskInfo = this.taskInfos[index]
        localStorage.setItem('taskInfo', JSON.stringify(taskInfo))
        this.$router.push('/board')
      },
      taskType: function (t) {
        let type = ['分类', '标框', '区域']
        return type[t]
      }
    }
  }
</script>

<style lang="stylus">
  .tasks
    .task-wrapper
      padding 2% 5%
      .title
        margin-bottom 20px
        color #409eff
        font-size 20px
      .tasks
        display flex
        flex-wrap: wrap
        align-content space-between
        .task-card
          margin-right 2%
          margin-bottom 50px
          width 23%
          flex 0 0 auto
          &:last-child
            margin-right 0
          .image
            width 100%
            height 200px
          .content
            position: relative;
            margin 15px 20px
            text-align center
            .task-title
              margin-bottom 20px
              font-size 18px
              font-weight 700
              color #333
            .info-blocks
              display flex
              .block
                flex: 1
                border-right 1px solid rgba(7, 17, 27, 0.1)
                &:last-child
                  border-right 0
                .label
                  margin-bottom 10px
                  color #999
                  font-size 16px
                .num
                  color #666
                  .unit
                    font-size 12px

</style>



