<template>
  <el-form class="login-form">
    <el-form-item>
      <el-input v-model="name" class="username-input" placeholder="手机号/邮箱">
        <i class="iconfont" slot="prefix">&#xe65a;</i>
      </el-input>
    </el-form-item>
    <el-form-item>
      <el-input v-model="password" class="password-input" placeholder="密码" type="password">
        <i class="iconfont" slot="prefix">&#xe62a;</i>
      </el-input>
    </el-form-item>
    <el-form-item>
      <el-button class="login-btn" @click="login" type="primary" round>登录</el-button>
    </el-form-item>
  </el-form>
</template>

<script type="text/ecmascript-6">
  export default {
    name: 'signin',
    data () {
      return {
        name: '',
        password: ''
      }
    },
    methods: {
      login: function () {
        this.$ajax.get('/user/login', {
          params: {
            name: this.name,
            password: this.password
          }
        }).then((response) => {
          response = response.data
          if (response.code === 0) {
            this.$router.push('/home')
          }
        })
      }
    }
  }
</script>

<style lang="stylus">
  .login-form
    width 25%
    margin 0 auto
    padding 2% 5%
    background #fff
    .login-btn
      width 100%
</style>



